import filterStyles from "./filterStyles.module.css";
import gameCharacterData from "./gameCharacters.json";
import unitData from "./unitProfiles.json";

import { useState } from "react";

import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Checkbox from "@mui/material/Checkbox";
import Container from "@mui/material/Container";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import FormControlLabel from "@mui/material/FormControlLabel";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import FilterAltIcon from "@mui/icons-material/FilterAlt";
import SortIcon from "@mui/icons-material/Sort";
import CropSquareRoundedIcon from "@mui/icons-material/CropSquareRounded";

export default function Filter({ filter, setFilter, rarity, skills }) {
  const [isOpen, setIsOpen] = useState({ filter: false, sort: false });
  const [dialogTempData, setDialogTempData] = useState(filter);

  const attr = ["cute", "cool", "pure", "happy", "mysterious"];

  const handleClick = (event, name) => {
    setDialogTempData(filter);
    setIsOpen({ ...isOpen, [name]: true });
  };

  const handleClose = (name) => {
    setIsOpen({ ...isOpen, [name]: false });
  };

  function getUnitVocals() {
    const units = Object.values(unitData).map((unit) => {
      const values = Object.values(gameCharacterData).filter(
        (item) => item.unit === unit.unit
      );
      return [unit.unit, values];
    });
    return Object.fromEntries(units);
  }
  const unitVocals = getUnitVocals();

  function rarityIcon(iconCount, iconType) {
    const result = [];
    for (let i = 0; i < iconCount; i++) {
      result.push(
        <img
          src={`https://raw.githubusercontent.com/Sekai-World/sekai-viewer/dev/src/assets/rarity_${iconType}.png`}
          alt={`rarityIcon`}
          width="25"
          loading="lazy"
        />
      );
    }
    return result;
  }
  return (
    <>
      <Box
        sx={{
          display: "grid",
          gridTemplateColumns: "auto 126px max-content",
          gap: 1,
          p: 1
        }}
      >
        <Box></Box>
        <Button
          variant="outlined"
          startIcon={<SortIcon />}
          disabled
          onClick={(event) => {
            handleClick(event, "sort");
          }}
        >
          ソート
        </Button>
        <Button
          variant="outlined"
          startIcon={<FilterAltIcon />}
          onClick={(event) => {
            handleClick(event, "filter");
          }}
        >
          フィルター
        </Button>
      </Box>
      <Dialog
        open={isOpen.filter}
        scroll={"paper"}
        fullWidth={true}
        maxWidth="xl"
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
      >
        <DialogTitle
          id="scroll-dialog-title"
          sx={{ pt: 3, backgroundColor: "primary.main" }}
        >
          <Box
            sx={{
              display: "grid",
              gridTemplateColumns: "max-content 1fr max-content"
            }}
          >
            <CropSquareRoundedIcon
              sx={{ transform: "rotate(45deg)", color: "primary.light" }}
            />
            <h3 style={{ textAlign: "center", color: "#FFF" }}>フィルター</h3>
            <CropSquareRoundedIcon
              sx={{ transform: "rotate(45deg)", color: "primary.light" }}
            />
          </Box>
        </DialogTitle>
        <DialogContent dividers={true} id="scroll-dialog-description">
          <div className={filterStyles.filterCategoryTitle}>
            <p>キャラクター</p>
            <div className={filterStyles.divider}></div>
          </div>
          <Box sx={{ py: 0.5 }}>
            <Box sx={{ py: 0.5 }}>
              <FormControlLabel
                control={
                  <Checkbox
                    defaultChecked
                    color="secondary"
                    checked={Object.values(dialogTempData.vocal).every(
                      (item) => item === true
                    )}
                    indeterminate={
                      !Object.values(dialogTempData.vocal).every(
                        (item) => item === dialogTempData.vocal[1]
                      )
                    }
                    onChange={(event) => {
                      const newState = dialogTempData;
                      Object.keys(newState.vocal).forEach((item) => {
                        newState.vocal[item] = event.target.checked;
                      });
                      setDialogTempData({ ...dialogTempData, ...newState });
                    }}
                  />
                }
                label={<p style={{ fontSize: 15 }}>全員を選択</p>}
                sx={{ gridRow: "1 / 10" }}
              />
            </Box>
            {Object.values(unitData).map((unit) => {
              return (
                <Box key={unit.unit}>
                  <div className={filterStyles.divider}></div>
                  <Box
                    className={`${filterStyles.filterGrid} ${filterStyles.vocal}`}
                    sx={{ py: 0.5 }}
                  >
                    <FormControlLabel
                      control={
                        <Checkbox
                          defaultChecked
                          color="secondary"
                          checked={unitVocals[unit.unit].every(
                            (item) => dialogTempData.vocal[item.id] === true
                          )}
                          indeterminate={
                            !unitVocals[unit.unit].every(
                              (item) =>
                                dialogTempData.vocal[item.id] ===
                                dialogTempData.vocal[
                                  unitVocals[unit.unit][0].id
                                ]
                            )
                          }
                          onChange={(event) => {
                            const newState = dialogTempData;
                            unitVocals[unit.unit].forEach((item) => {
                              newState.vocal[item.id] = event.target.checked;
                            });
                            setDialogTempData({
                              ...dialogTempData,
                              ...newState
                            });
                          }}
                        />
                      }
                      label={
                        <img
                          src={`https://raw.githubusercontent.com/Sekai-World/sekai-viewer/dev/public/images/jp/logol_outline/logo_${unit.unit}.png`}
                          alt={unit.unitName}
                          height={40}
                          width={60}
                          style={{ objectFit: "contain" }}
                        />
                      }
                      sx={{ gridRow: "1 / 10" }}
                    />
                    {Object.values(gameCharacterData)
                      .filter((item) => item.unit === unit.unit)
                      .map((character) => {
                        return (
                          <FormControlLabel
                            key={`ボーカル${character.name}`}
                            control={
                              <Checkbox
                                defaultChecked
                                color="secondary"
                                checked={dialogTempData.vocal[character.id]}
                                onChange={(event) => {
                                  const newState = dialogTempData;
                                  newState.vocal[character.id] =
                                    event.target.checked;
                                  setDialogTempData({
                                    ...dialogTempData,
                                    ...newState
                                  });
                                }}
                              />
                            }
                            label={
                              <img
                                src={`https://raw.githubusercontent.com/Sekai-World/sekai-viewer/dev/src/assets/chara_icons/chr_ts_${character.id}.png`}
                                alt={character.name}
                                height={30}
                                width={30}
                                style={{ objectFit: "contain" }}
                              />
                            }
                            sx={{
                              ".MuiFormControlLabel-label": {
                                fontSize: 15,
                                display: "flex",
                                alignItems: "center"
                              }
                            }}
                          />
                        );
                      })}
                  </Box>
                </Box>
              );
            })}
          </Box>

          <div className={filterStyles.filterCategoryTitle}>
            <p>ユニット・サブユニット</p>
            <div className={filterStyles.divider}></div>
          </div>
          <FormControl>
            <RadioGroup
              row
              defaultValue="all"
              value={dialogTempData.unit}
              onChange={(event) =>
                setDialogTempData({
                  ...dialogTempData,
                  unit: event.target.value
                })
              }
            >
              <FormControlLabel
                value="all"
                control={<Radio color="secondary" />}
                label={<p>すべて</p>}
              />
              {Object.values(unitData).map((unit) => {
                return (
                  <FormControlLabel
                    value={unit.unit}
                    control={<Radio color="secondary" />}
                    label={
                      <img
                        src={`https://raw.githubusercontent.com/Sekai-World/sekai-viewer/dev/public/images/jp/logol_outline/logo_${unit.unit}.png`}
                        alt={unit.unitName}
                        height={40}
                        width={60}
                        style={{ objectFit: "contain" }}
                      />
                    }
                  />
                );
              })}
            </RadioGroup>
          </FormControl>

          <div className={filterStyles.filterCategoryTitle}>
            <p>タイプ</p>
            <div className={filterStyles.divider}></div>
          </div>
          <FormControl>
            <RadioGroup
              row
              defaultValue="all"
              onChange={(event) =>
                setDialogTempData({
                  ...dialogTempData,
                  type: event.target.value
                })
              }
            >
              <FormControlLabel
                value="all"
                control={<Radio color="secondary" />}
                label={<p>すべて</p>}
              />
              {attr.map((item) => {
                return (
                  <FormControlLabel
                    value={item}
                    control={<Radio color="secondary" />}
                    label={
                      <img
                        src={`https://pjsek.ai/images/member/icon_attribute_${item}.png`}
                        alt={item}
                        height={30}
                        width={30}
                        style={{ objectFit: "contain" }}
                      />
                    }
                  />
                );
              })}
            </RadioGroup>
          </FormControl>

          <div className={filterStyles.filterCategoryTitle}>
            <p>レアリティ</p>
            <div className={filterStyles.divider}></div>
          </div>
          <Box sx={{ py: 0.5 }}>
            <Box sx={{ py: 0.5 }}>
              <FormControlLabel
                control={
                  <Checkbox
                    defaultChecked
                    color="secondary"
                    checked={Object.values(dialogTempData.rarity).every(
                      (item) => item === true
                    )}
                    indeterminate={
                      !Object.values(dialogTempData.rarity).every(
                        (item) => item === dialogTempData.rarity["rarity_1"]
                      )
                    }
                    onChange={(event) => {
                      const newState = dialogTempData;
                      Object.keys(newState.rarity).forEach((item) => {
                        newState.rarity[item] = event.target.checked;
                      });
                      setDialogTempData({ ...dialogTempData, ...newState });
                    }}
                  />
                }
                label={<p style={{ fontSize: 15 }}>すべて選択</p>}
                sx={{ gridRow: "1 / 10" }}
              />
            </Box>
            <div className={filterStyles.divider}></div>
            {Object.entries(rarity).map((item) => {
              return (
                <Box sx={{ display: "inline-flex", py: 0.5 }}>
                  <FormControlLabel
                    control={
                      <Checkbox
                        defaultChecked
                        color="secondary"
                        checked={dialogTempData.rarity[item[0]]}
                        onChange={(event) => {
                          const newState = dialogTempData;
                          newState.rarity[item[0]] = event.target.checked;
                          setDialogTempData({
                            ...dialogTempData,
                            ...newState
                          });
                        }}
                      />
                    }
                    label={rarityIcon(item[1].count, item[1].type)}
                  />
                </Box>
              );
            })}
          </Box>
          <div className={filterStyles.filterCategoryTitle}>
            <p>スキル</p>
            <div className={filterStyles.divider}></div>
          </div>
          <Box sx={{ py: 0.5 }}>
            <Box sx={{ py: 0.5 }}>
              <FormControlLabel
                control={
                  <Checkbox
                    defaultChecked
                    color="secondary"
                    checked={Object.values(dialogTempData.skill).every(
                      (item) => item === true
                    )}
                    indeterminate={
                      !Object.values(dialogTempData.skill).every(
                        (item) => item === dialogTempData.skill["scoreUp"]
                      )
                    }
                    onChange={(event) => {
                      const newState = dialogTempData;
                      Object.keys(newState.skill).forEach((item) => {
                        newState.skill[item] = event.target.checked;
                      });
                      setDialogTempData({ ...dialogTempData, ...newState });
                    }}
                  />
                }
                label={<p style={{ fontSize: 15 }}>すべて選択</p>}
                sx={{ gridRow: "1 / 10" }}
              />
            </Box>
            <div className={filterStyles.divider}></div>
            {Object.entries(skills).map((item) => {
              return (
                <Box sx={{ display: "inline-flex", py: 0.5 }}>
                  <FormControlLabel
                    control={
                      <Checkbox
                        defaultChecked
                        color="secondary"
                        checked={dialogTempData.skill[item[0]]}
                        onChange={(event) => {
                          const newState = dialogTempData;
                          newState.skill[item[0]] = event.target.checked;
                          setDialogTempData({
                            ...dialogTempData,
                            ...newState
                          });
                        }}
                      />
                    }
                    label={<p>{item[1].description}</p>}
                  />
                </Box>
              );
            })}
          </Box>
        </DialogContent>
        <DialogActions>
          <Container
            sx={{
              display: "grid",
              gridTemplateColumns: "repeat(2, min(50%, 230px))",
              justifyContent: "center",
              gap: "14px",
              height: "50px"
            }}
          >
            <Box
              className={filterStyles.cancelButton}
              onClick={() => {
                handleClose("filter");
              }}
            >
              <div>
                <div className={filterStyles.dot}></div>
                <p>キャンセル</p>
              </div>
            </Box>
            <Box
              className={filterStyles.confirmButton}
              onClick={() => {
                handleClose("filter");
                setFilter(dialogTempData);
              }}
            >
              <div>
                <div className={filterStyles.dot}></div>
                <p>決定</p>
              </div>
            </Box>
          </Container>
        </DialogActions>
      </Dialog>
    </>
  );
}
