import CardData from "./Cards.json";
import gameCharacterData from "./gameCharacters.json";
import unitData from "./unitProfiles.json";
import "./titleSticky.css";
import Filter from "./Filter.js";

import { useState, useMemo } from "react";

import Container from "@mui/material/Container";
import Toolbar from "@mui/material/Toolbar";
import Grid from "@mui/material/Unstable_Grid2";
// import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
// import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Stack from "@mui/material/Stack";
// import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Divider from "@mui/material/Divider";

// const drawerWidth = 240;

export default function Cards() {
  const rarity = {
    rarity_1: { count: 1, type: "star_normal" },
    rarity_2: { count: 2, type: "star_normal" },
    rarity_3: { count: 3, type: "star_afterTraining" },
    rarity_4: { count: 4, type: "star_afterTraining" },
    rarity_birthday: { count: 1, type: "birthday" }
  };
  const skills = {
    scoreUp: { id: [1, 2, 3, 4], description: "スコアUP" },
    scoreUpPerfect: { id: [11], description: "PERFECTのみスコアUP" },
    scoreUpKeep: { id: [13], description: "GREAT以下を出すまでスコアUP" },
    scoreUpConditionlife: { id: [12], description: "ライフ参照スコアUP" },
    scoreUpUnit: {
      id: [15, 16, 17, 18, 19],
      description: "ユニット統一スコアUP"
    },
    judgmentUp: { id: [5, 6, 7], description: "判定強化" },
    lifeRecovery: { id: [8, 9, 10], description: "ライフ回復" },
    lifeRecoveryPerfect: {
      id: [14],
      description: "ライフ回復・PERFECTのみスコアUP"
    }
  };

  const [filter, setFilter] = useState({
    vocal: Object.keys(gameCharacterData).reduce((accumlator, current) => {
      accumlator[current] = true;
      return accumlator;
    }, {}),
    unit: "all",
    type: "all",
    rarity: Object.keys(rarity).reduce((accumlator, current) => {
      accumlator[current] = true;
      return accumlator;
    }, {}),
    skill: Object.keys(skills).reduce((accumlator, current) => {
      accumlator[current] = true;
      return accumlator;
    }, {})
  });

  function getRarityIcon(cardRarityType) {
    const normalIcon = "star_normal";
    const afterTrainingIcon = "star_afterTraining";
    const birthdayIcon = "birthday";
    const result = [];
    const iconCount = { count: 0, type: "" };

    switch (cardRarityType) {
      case "rarity_birthday":
        iconCount.count = 1;
        iconCount.type = birthdayIcon;
        break;
      case "rarity_1":
        iconCount.count = 1;
        iconCount.type = normalIcon;
        break;
      case "rarity_2":
        iconCount.count = 2;
        iconCount.type = normalIcon;
        break;
      case "rarity_3":
        iconCount.count = 3;
        iconCount.type = afterTrainingIcon;
        break;
      case "rarity_4":
        iconCount.count = 4;
        iconCount.type = afterTrainingIcon;
        break;

      default:
        break;
    }
    for (let i = 0; i < iconCount.count; i++) {
      result.push(
        <img
          src={`https://raw.githubusercontent.com/Sekai-World/sekai-viewer/dev/src/assets/rarity_${iconCount.type}.png`}
          alt={`rarityIcon`}
          width="35"
          loading="lazy"
        />
      );
    }
    return result;
  }

  const filteredCardData = useMemo(() => {
    const characterFilter = Object.keys(filter.vocal).filter(
      (item) => filter.vocal[item]
    );
    const rarityFilter = Object.keys(filter.rarity).filter(
      (item) => filter.rarity[item]
    );
    const skillFilter = Object.keys(filter.skill)
      .filter((item) => filter.skill[item])
      .flatMap((item) => skills[item].id);

    return CardData.filter((item) => {
      const vocal = characterFilter.includes(String(item.characterId));
      const unit =
        item.supportUnit === filter.unit ||
        gameCharacterData[item.characterId].unit === filter.unit;
      const type = item.attr === filter.type;
      const rarity = rarityFilter.includes(item.cardRarityType);
      const skill = skillFilter.includes(item.skillId);

      if (filter.unit === "all" && filter.type === "all") {
        return vocal && rarity && skill;
      } else if (filter.unit === "all") {
        return vocal && type && rarity && skill;
      } else if (filter.type === "all") {
        return vocal && unit && rarity && skill;
      } else {
        return vocal && unit && type && rarity && skill;
      }
    });
  }, [filter]);

  console.log(filter);

  return (
    <Container component="main" sx={{ flexGrow: 1, p: 2 }}>
      <Toolbar
        sx={{
          position: "sticky",
          top: "32px",
          background: "rgb(243, 243, 243)",
          zIndex: "10",
          margin: "0 -16px"
        }}
      />

      <div
        style={{
          margin: "8px -16px 8px",
          padding: "0 16px",
          position: "sticky",
          top: "88px",
          background: "rgb(243, 243, 243)",
          zIndex: "10"
        }}
      >
        <div style={{ display: "flex" }}>
          <h2>全カード</h2>
          <Filter
            filter={filter}
            setFilter={setFilter}
            rarity={rarity}
            skills={skills}
          />
        </div>
        <Divider />
      </div>
      <Grid container spacing={2}>
        {filteredCardData.map((item) => {
          return (
            <Grid xs={12} sm={6} md={4} lg={3}>
              <Card>
                <CardMedia sx={{ position: "relative", height: "200px" }}>
                  <img
                    src={`https://pjsek.ai/images/member/icon_attribute_${item.attr}.png`}
                    alt={`${item.attr}icon`}
                    width="35"
                    loading="lazy"
                    style={{ position: "absolute", right: "0" }}
                  />
                  <div
                    style={{
                      display: "flex",
                      flexFlow: "column",
                      position: "absolute",
                      left: "0",
                      bottom: "0"
                    }}
                  >
                    {getRarityIcon(item.cardRarityType)}
                  </div>

                  {item.cardRarityType !== "rarity_3" &&
                  item.cardRarityType !== "rarity_4" ? (
                    <img
                      src={`https://assets.pjsek.ai/file/pjsekai-assets/startapp/character/member_small/${item.assetbundleName}/card_normal.png`}
                      alt=""
                      loading="lazy"
                      className={"cardImg"}
                      style={{
                        height: "200px",
                        width: "100%",
                        objectFit: "cover"
                      }}
                    />
                  ) : (
                    <Stack
                      direction="row"
                      sx={{ width: "100%" }}
                      justifyContent="center"
                    >
                      <img
                        src={`https://assets.pjsek.ai/file/pjsekai-assets/startapp/character/member_small/${item.assetbundleName}/card_normal.png`}
                        alt=""
                        loading="lazy"
                        className={"cardImg"}
                        onMouseOver={(event) => {
                          const fullWidth =
                            event.target.parentElement.clientWidth;
                          let targetWidth;
                          if (fullWidth * 0.8 < 354) {
                            targetWidth = fullWidth * 0.8;
                          } else {
                            targetWidth = 354;
                          }
                          event.target.style.width = `${targetWidth}px`;
                          event.target.nextElementSibling.style.width = `${
                            fullWidth - targetWidth
                          }px`;
                        }}
                        onMouseLeave={(event) => {
                          event.target.style.width = "50%";
                          event.target.nextElementSibling.style.width = "50%";
                        }}
                      />
                      <img
                        src={`https://assets.pjsek.ai/file/pjsekai-assets/startapp/character/member_small/${item.assetbundleName}/card_after_training.png`}
                        alt=""
                        loading="lazy"
                        className={"cardImg"}
                        onMouseOver={(event) => {
                          const fullWidth =
                            event.target.parentElement.clientWidth;
                          let targetWidth;
                          if (fullWidth * 0.8 < 354) {
                            targetWidth = fullWidth * 0.8;
                          } else {
                            targetWidth = 354;
                          }
                          event.target.style.width = `${targetWidth}px`;
                          event.target.previousElementSibling.style.width = `${
                            fullWidth - targetWidth
                          }px`;
                        }}
                        onMouseLeave={(event) => {
                          event.target.style.width = "50%";
                          event.target.previousElementSibling.style.width =
                            "50%";
                        }}
                      />
                    </Stack>
                  )}
                </CardMedia>
                <CardContent>
                  <Typography gutterBottom variant="h6" component="h6">
                    {item.prefix}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {gameCharacterData[item.characterId].name}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {new Date(item.releaseAt).toLocaleDateString()}
                  </Typography>
                </CardContent>
                {/* <CardActions>
                  <Button size="small">Share</Button>
                  <Button size="small">Learn More</Button>
                </CardActions> */}
              </Card>
            </Grid>
          );
        })}
      </Grid>
    </Container>
  );
}
